@media (width <=576px) {

  #home {
    background: #272D69;
  }

  h1 {
    font-size: 2.5rem;
  }

  p {
    font-size: 1rem;
  }

  .main-btn {
    width: 7rem;
    font-size: 0.75rem;
    height: 2.5rem;
  }

  .about-section .text-sec {
    padding-left: 1rem;
  }

  .sec-head {
    margin-top: 1rem;
    font-size: 2.5rem;
    line-height: 2.5rem;
  }

  .second-para {
    font-size: 0.685rem;
  }

  .second-page-ul {
    margin-top: 10px;
    font-size: 0.75rem;
  }

  .second-sec-page-ul {
    font-size: 0.75rem;
  }

  /* .second-sec-page-ul .second-sec-page-li {
    padding: 3px;
  } */

  .download-btn {
    width: 7rem;
  }

  .download-app-btn {
    width: 9rem;
  }

  .third-iphone-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
  }

  #third {
    background: #ECEDF5;
    position: relative;
  }
  
  #third::before {
    content: '';
    position: absolute;
    background-color: #FFc4b0;
    width: 100%;
    height: 35%;
    left: 0;
  }

  .iphone-image {
    margin: 40px 60px;
  }

  .news-iphone {
    display: none;
  }

  .news-heading {
    /* margin-top: 3.725rem; */
    line-height: 2.3rem;
    font-size: 1.7rem;
    margin: 0;
  }

  .news-para {
    font-size: 0.85rem;
  }

  .third-heading {
    margin-top: 0.5rem;
    font-size: 1.75rem;
    line-height: 1.5rem;
  }

  .third-para {
    font-size: 0.75rem;
    line-height: 0.75rem;
  }

  .iphone-image {
    margin: 70px 20px;
    width: 9.81388rem;
  }

  .designed .designed-in {
    font-size: 10px;
  }

  .links .link {
    font-size: 10px;
    margin-right: 0.5rem;
  }

  .carousel-fifth {
    height: 470px;
  }

  .fifth {
    padding: 75px 0;
  }

  .quote {
    font-size: 1rem;
    margin-top: 1.35rem;
    margin-bottom: 2rem;
  }

  .fifth-para {
    margin-bottom: 30px;
    font-size: 18px;
  }

  .designed {
    padding: 0.5rem;
    margin-top: 1rem;
  }
  
  .links {
    margin-top: 0.5rem;
    padding: 0.5rem;
  }

  .newsletter {
    padding: 40px 10px 100px 10px;
  }
}

@media(min-width: 768px) {
  .news-iphone {
    margin-left: 5rem;
  }

  .card-img-top {
    width: 52px;
    height: 47px;
    margin-top: 20px;
  }

  .card-img-top-sm {
    width: 28px;
    height: 25px;
    margin-top: 20px;
  }
}

